<template>
  <div class="container">
    <h2 class="text-center">題庫管理</h2>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.go(-1)">返回上一頁</button>
    <!-- 訊息提示框 -->
    <Alert
      :MODE="MSG.MODE"
      :MSGTYPE="MSG.TYPE"
      :TITLE="MSG.TITLE"
      :TEXT="MSG.TEXT"
      @alert-check="(isClick, OKorCancel) => (alertCheck = { isClick, OKorCancel })"
    />
    <form class="row">
      <!-- 上傳檔案input的部分 -->
      <div class="col-12">
        <div class="input-group mb-3">
          <input type="file" class="form-control" @change="onFileSelected" />
        </div>
        <div class="progress">
          <div
            class="progress-bar progress-bar-striped progress-bar-animated"
            role="progressbar"
            aria-valuenow="75"
            aria-valuemin="0"
            aria-valuemax="100"
            :style="'width:' + uploadPercentage + '%'"
          >
            {{ uploadPercentage }}%
          </div>
        </div>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-success btn-lg" type="button" @click="uploadFile()">新增題組</button>
      </div>
    </form>
    <p v-if="questionGroupData.length === 0" class="text-danger fw-bold fs-5 text-center">目前尚未上傳題組。</p>
    <form v-else class="row">
      <div class="col-12">
        <table class="table">
          <thead>
            <tr>
              <td>QGID</td>
              <td>題組</td>
              <td>選擇</td>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in questionGroupData" :class="item.status ? 'table-success' : 'table-danger'">
              <td>{{ item.QGID }}</td>
              <td>{{ item.subject }}</td>
              <td>
                <input class="form-check-input" type="radio" name="Radio" :value="item.QGID" v-model="selectQGID" />
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="col-12 d-grid gap-2 my-2">
        <button class="btn btn-primary btn-lg" type="button" @click="selectDeal()">執行</button>
      </div>
    </form>
  </div>
</template>

<script>
import router from "@/router";
import Alert from "@/components/Alert.vue";

export default {
  name: "QuestionManage",
  components: {
    Alert,
  },
  data() {
    return {
      // 訊息提示框資料容器
      MSG: {
        MODE: "",
        TYPE: "",
        TITLE: "",
        TEXT: [],
      },
      // 訊息確認[開關]
      alertCheck: false,
      // 訊息功能碼
      funcNum: 0,
      selectedFile: null,
      uploadPercentage: 0,
      questionGroupData: [],
      selectQGID: "",
    };
  },
  methods: {
    onFileSelected(event) {
      this.selectedFile = event.target.files[0];
    },
    uploadFile() {
      // 上傳檔案
      if (this.selectedFile === null) {
        // 訊息提示框
        this.alertVisible = true;
        this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
        this.funcNum = -1;
        this.MSG.TYPE = "warning"; // primary成功, warning警告, danger錯誤
        this.MSG.TITLE = "系統警告"; // 系統提示，系統警告，系統錯誤
        this.MSG.TEXT = ["請選擇檔案。"]; // 內容
        return;
      }
      // 限制檔案副檔名
      if (this.selectedFile.name.split(".").pop() !== "xlsx" && this.selectedFile.name.split(".").pop() !== "xls") {
        // 訊息提示框
        this.alertVisible = true;
        this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
        this.funcNum = -1;
        this.MSG.TYPE = "warning"; // primary成功, warning警告, danger錯誤
        this.MSG.TITLE = "系統警告"; // 系統提示，系統警告，系統錯誤
        this.MSG.TEXT = ["檔案格式錯誤", "請選擇正確的檔案格式。", "僅允許.xls及.xlsx"]; // 內容
        return;
      }
      const fd = new FormData();
      fd.append("file", this.selectedFile, this.selectedFile.name);
      this.$http
        .post("api/questionManage/uploadQuestionGroup", fd, {
          onUploadProgress: (progressEvent) => {
            this.uploadPercentage = parseInt(Math.round((progressEvent.loaded / progressEvent.total) * 100));
          },
        })
        .then((res) => {
          setTimeout(() => {
            this.uploadPercentage = 0;
            this.alertVisible = false;
          }, 1000);
          if (res.data.code === 200) {
            // 訊息提示框
            this.alertVisible = true;
            this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
            this.funcNum = -1;
            this.MSG.TYPE = "primary"; // primary成功, warning警告, danger錯誤
            this.MSG.TITLE = "系統提示"; // 系統提示，系統警告，系統錯誤
            this.MSG.TEXT = [];
            this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
            // 取得題組資料
            this.$http
              .get("api/questionManage/getQuestionGroups")
              .then((res) => {
                this.questionGroupData = res.data.data;
              })
              .catch((err) => {
                this.alertVisible = true;
                this.MSG.MODE = "OKOnly";
                this.funcNum = -1;
                this.MSG.TYPE = "danger";
                this.MSG.TITLE = "系統錯誤";
                this.MSG.TEXT = [];
                this.MSG.TEXT.push(err);
              });
          } else {
            // 訊息提示框
            this.alertVisible = true;
            this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
            this.funcNum = -1;
            this.MSG.TYPE = "warning"; // primary成功, warning警告, danger錯誤
            this.MSG.TITLE = "系統警告"; // 系統提示，系統警告，系統錯誤
            this.MSG.TEXT = [];
            this.MSG.TEXT = this.MSG.TEXT.concat(res.data.msg);
            if (res.data.sys !== "") this.MSG.TEXT.push(res.data.sys);
          }
        })
        .catch((err) => {
          this.alertVisible = true;
          this.MSG.MODE = "OKOnly";
          this.funcNum = -1;
          this.MSG.TYPE = "danger";
          this.MSG.TITLE = "系統錯誤";
          this.MSG.TEXT = [];
          this.MSG.TEXT.push(err);
        });
    },
    selectDeal() {
      if (this.selectQGID !== "") {
        // 取得選擇的題組資料
        const data = this.questionGroupData.find((item) => item.QGID === this.selectQGID);
        // 把選擇的題組資料存到Vuex
        this.$store.commit("SET_QUEST_GROUP_EDIT", data);
        router.push("/questionEdit");
      } else {
        // 訊息提示框
        this.alertVisible = true;
        this.MSG.MODE = "OKOnly"; // 形式為：OKOnly、OKCancel
        this.funcNum = -1;
        this.MSG.TYPE = "warning"; // primary成功, warning警告, danger錯誤
        this.MSG.TITLE = "系統警告"; // 系統提示，系統警告，系統錯誤
        this.MSG.TEXT = ["請選擇題組。"]; // 內容
      }
    },
    hideAlert() {
      this.$store.commit("SET_ALERT_VISIBLE", false);
      this.alertVisible = false;
    },
  },
  created() {
    // 取得題組資料
    this.$http
      .get("api/questionManage/getQuestionGroups")
      .then((res) => {
        this.questionGroupData = res.data.data;
      })
      .catch((err) => {
        this.alertVisible = true;
        this.MSG.MODE = "OKOnly";
        this.funcNum = -1;
        this.MSG.TYPE = "danger";
        this.MSG.TITLE = "系統錯誤";
        this.MSG.TEXT = [];
        this.MSG.TEXT.push(err);
      });
  },
  mounted() {},
  computed: {
    // alert開關連動狀態功能
    alertVisible: {
      get() {
        return this.$store.state.alertVisible;
      },
      set(value) {
        this.$store.commit("SET_ALERT_VISIBLE", value);
      },
    },
  },
  watch: {
    alertCheck: function (val) {
      if (val.isClick) {
        if (val.OKorCancel === "OK") {
          if (this.funcNum === 0) {
            return;
          } else if (this.funcNum === -1) {
            this.hideAlert();
          } else if (this.funcNum === -2) {
            this.hideAlert();
            this.$router.go(-1);
          }
        } else if (val.OKorCancel === "Cancel") {
          this.hideAlert();
        }
      }
    },
  },
};
</script>

<style lang="scss"></style>
